/* gogle font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

ul, li {
    list-style: none;
}

a {
    text-decoration: none;
    color: #000;
}

h1 {
    text-align: center;
    font-size: 40px;
    letter-spacing: 2px;
    text-shadow: 3px 3px 3px rgba(0, 0, 0, .35);
}

.container {
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    padding: 0px 30px;
}

.container .content {
    text-align: center;
    width: 400px;
    margin: 10px auto;
    border: 1px solid rgba(0, 0, 0, .35);
    box-shadow: inset 5px 5px 10px rgba(0, 0, 0, .15),
        3px 3px 6px rgba(0, 0, 0, .25);
}

.container .content h2 {
    font-size: 22px;
    margin: 3px 0px;
}

.content img {
    margin-top: 10px;
    vertical-align: middle;
    max-width: 100%;
}

.content p {
    margin: 5px 0;
    padding: 0px 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 14px;
}

.content ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.content ul li {
    margin: 0 10px;
    font-size: 16px;
}

@media (max-width: 564px) {
    .content {
        width: 300px;
    }
    .content p {
        font-size: 12px;
    }
    .content ul li {
        font-size: 13px;
    }
}